html {
    /* font-family: "Outfit", sans-serif; */
}

nav {
    padding: 1.5rem;
    position: fixed;
    top: 0%;
    left: 50%;
    transform: translateX(-50%);
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: #fff;
    z-index: 1000;
}

.dropdown-menu {
    position: absolute;
    top: 100%;
    left: 0;
    z-index: 1;
    display: none;
    min-width: 120px;
    padding: 8px 0;
    background-color: #fff;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    border-radius: 4px;
}

.welcome-user {
    position: relative;
    cursor: pointer;
}

.welcome-user:hover .dropdown-menu {
    display: block;
}

.dropdown-menu li {
    display: block;
    width: 150px;
    padding:5px;
}

.dropdown-menu li a {
    color: #333;
    text-decoration: none;
    transition: color 0.3s ease;
}

.dropdown-menu li a:hover {
    color: #2190ff;
}

.nav__logo {
    margin-left: 20px;
    cursor: pointer;
}


.nav__links {
    list-style: none;
    display: flex;
    align-items: center;
    gap: 1rem;
    font-size: 1rem;
    font-weight: 500;
}

.nav__links > li {
    margin-left: 10px;
}

.nav__links > li:first-child {
    margin-left: 0;
}

.nav__icon {
    display: none; 
}


.link a {
    padding: 0 1rem;
    color: #0d213f;
    transition: 0.3s;
    text-decoration: none;
    font-weight: bold;
    white-space: nowrap;
}

.link a:hover {
    color: #3685fb;
}
/* Media queries */
@media (max-width: 768px) {
    .nav__logo {
        margin-right: auto;
    }

    .nav__icon {
        display: block; 
        cursor: pointer;
        margin-right: 20px;
    }

    .nav__links {
        display: none;
        flex-direction: column;
        align-items: center;
        gap: 0.5rem;
        width: 100%;
    }
    .nav__links.active {
        display: flex;
        position: absolute;
        top: 100%;
        left: 0;
        width: 100%;
        background-color: #fff;
        flex-direction: column;
        align-items: center; 
        padding: 1rem 0;
        border-radius: 0.5rem;
        box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
        z-index: 999;
        animation: slideDown 0.5s ease-in-out; 
        margin: 0; 
    }

    .nav__links > li {
        margin-left: 0 !important; 
        display: flex;
        justify-content: center;
    }
}
/* Keyframes for slide down animation */
@keyframes slideDown {
    0% {
        transform: translateY(-10px);
        opacity: 0;
    }
    100% {
        transform: translateY(0);
        opacity: 1;
    }
}
