.profile-link {
  position: relative;
}

.profile-dropdown {
  position: absolute;
  top: 100%;
  left: 0;
  background: #fff;
  min-width: 140px;
  box-shadow: 0 2px 8px rgba(0,0,0,0.12);
  border-radius: 6px;
  z-index: 100;
  padding: 8px 0;
}

.profile-dropdown-item {
  display: block;
  padding: 8px 18px;
  color: #333;
  text-decoration: none;
  font-weight: 500;
  transition: background 0.2s;
}

.profile-dropdown-item:hover {
  background: #f0f0f0;
}